@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
.nav-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(18, 18, 18, 1);
    color:  #FBFAF5;
    height: 4rem;
    font-family: 'Oswald', sans-serif;
}

.return {
    width: auto;
    position: absolute;
    left: 0;
    margin-left: 3em;
    height: 3em;
    font-family: 'Oswald', sans-serif;
    cursor: pointer;
}

.logo {
    font-size: 3rem;
}

.view-leaderboard {
    width: auto;
    position: absolute;
    right: 0;
    margin-right: 3em;
    height: 3em;
    font-family: 'Oswald', sans-serif;
    cursor: pointer;
}


.board-item {
    width: 300px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    outline: 1px solid rgba(0,0,0,0.2);
    transition: 0.3s;
    display: flex;
    flex-flow: column;
}

.board-item:hover {
    box-shadow: 0 16px 16px 0 rgba(0,0,0,0.3);
    transition: 0.3s;
    cursor: pointer;
    transform: scale(1.1, 1.1);
}

.board-item-img {
    width: 300px;
    height: auto;
}

.board-item-container {
    height: auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.board-item-name {
    font-family: 'Oswald', sans-serif;
    font-weight: 600;
    text-align: center;
    font-size: 1.6rem;
    margin: 0.2em 0 0.2em 0;
    color: rgba(18, 18, 18, 1);
}
.spinner {
  margin: 30vh auto auto auto;
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
.board-container {
    display: grid;
    grid-template-columns: repeat(5, 300px);
    grid-template-rows: repeat(2);
    grid-gap: 2em;
    justify-content: center;
    margin: 2em 0 2em 0;
    
}

.select-tag {
    color:  #FBFAF5;
    text-align: center;
    font-family: 'Oswald', sans-serif;
    font-size: 5rem;
    margin-top: 0.4em;
    text-shadow: 2px 2px black;
}
.img-container {
    display: flex;
    justify-content: center;
    position: relative;

}

.image {
    height: 100%;
    width: auto;
    position: relative;
    top: 10px
}

.image:hover {
    cursor: crosshair;
}

.character-display-board {
    position: absolute;
}

.character-container-board {
    display: flex;
    flex-flow: column;
    text-align: center;
    color:  #FBFAF5;
    font-family: 'Oswald', sans-serif;
    margin: 0 0 0 2em;
}

.character-container-board img {
    width: 100px;
    height: 100px;
    margin: 0 0 1em 0;
    opacity: 0.30;
}

.found-character {
    display: flex;
    flex-flow: row;
    align-items: center;
}

.dropdown {
    position: absolute;
    width: 150px;
    font-size: 1.2rem;
    z-index: 2;
    background-color: white; 
}

.dropdown-ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.dropdown-li {
    padding: 8px 12px;
    border: 1px solid black;
}

.dropdown-li:hover {
    cursor: pointer;
    background-color: grey;
}
.rules-container {
    display: flex;
    align-items: center;
    flex-flow: column;
    width: 50%;
    height: auto;
    background-color: white;
    margin: 10em auto 0 auto;
    border: 4px solid;
    border-color: rgb(18, 18, 18);
    border-radius: 25px;
    font-family: 'Oswald', sans-serif;
}

.rules-title {
    font-size: 3rem;
}

.instructions {
    font-size: 1.6rem;
}

.character-display {
   display: flex;
   width: 100%;
   flex-flow: row;
   justify-content: space-evenly;
   align-items: space-between;
   margin-bottom: 4em;
}
.character-container {
    display: block;
    text-align: center;
}

.character-container img {
    width: 100px;
    height: 100px;
}

.game-start {
    background-color: #48c43b;
    font-size: 2rem;
    width: 40%;
    height: 10%;
    cursor: pointer;
    margin: auto 0 2em 0;
}
.endscreen-wrapper {
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    margin: auto;
    position: absolute;
    z-index: 2;
}


.endscreen-container {
    display: flex;
    align-items: center;
    flex-flow: column;
    width: 50%;
    height: 50vh;
    background-color: rgb(19, 19, 19);
    color:  #FBFAF5;
    margin: 10em auto 0 auto;
    border: 4px solid;
    border-color: rgb(18, 18, 18);
    border-radius: 25px;
    font-family: 'Oswald', sans-serif;
    position: relative ;
}

.endscreen-container h1 {
    font-size: 3rem;
}

.endscreen-container p {
    margin: 0 auto 0.5em auto;
    font-size: 2rem;
}

.username-input {
    font-size: 2rem;
    margin: 0.5em auto 1.5em auto;
    width: 50%;
}

.button-container {
    display: flex;
    justify-content: center;
    grid-gap: 2em;
    gap: 2em;
    width: 80%;
}

.button-container button {
    font-family: 'Oswald', sans-serif;
    color:  #FBFAF5;
    font-size: 2rem;
    width: 50%;
    height: 100%;
    border: none;
    border-radius: 12px;
}

.cancel {
    background-color: #e01f28;
    cursor: pointer;
}

.cancel:hover {
    background-color: #ad1921;
}

.save {
    background-color: #41c936;
    cursor: pointer;
}

.save:hover {
    background-color: #2f9628;
}
body {
    margin: 0;
    background-image: url(https://wallpaperaccess.com/full/1092758.jpg);
    background-size: cover;
    background-color: rgb(59, 59, 59);
    min-height: 100vh;
    max-height: 100%
}

.transition-wrapper {
	transition: opacity 400ms 0ms ease-in;
    opacity: 0;
}

.show {
    opacity: 1;
}
.leaderboard-container {
    color:  #FBFAF5;
    font-family: 'Oswald', sans-serif;
    text-align: center;
}

.current-leaderboard-title h1 {
    font-size: 4rem;
    text-align: center;
    width: 20rem;
}

.current-leaderboard-title {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 2em;
    gap: 2em;
}

.scoreboard-container {
    background-color: rgba(18, 18, 18, 0.76);
    display: grid;
    width: 50%;
    grid-template-columns: repeat(1, 10rem);
    justify-content: center;
    margin: auto;
    padding-bottom: 6em;
}

.scoreboard-labels, .scoreboard-info {
    font-size: 1.6rem;
    display: grid;
    grid-template-columns: repeat(2, 8em);
    justify-content: center;
    text-align: left;
    grid-gap: 5em;
    gap: 5em;
    
   
}

.arrow {
    cursor: pointer;
}

.scoreboard-labels h2 {
    background-color: rgb(25, 37, 78);
    border-bottom: 2px solid white;
    margin-bottom: 1em;
}

.scoreboard-info p {
    background-color: rgb(25, 37, 78);
}

